@font-face {
  font-family: "Lato";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/lato-v15-latin-700.woff2") format("woff2"), url("../fonts/lato-v15-latin-700.woff") format("woff"); }

@font-face {
  font-family: "Lato";
  font-weight: 900;
  font-display: swap;
  src: url("../fonts/lato-v15-latin-900.woff2") format("woff2"), url("../fonts/lato-v15-latin-900.woff") format("woff"); }

@font-face {
  font-family: "Lato";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/lato-v15-latin-regular.woff2") format("woff2"), url("../fonts/lato-v15-latin-regular.woff") format("woff"); }

* {
  box-sizing: border-box; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

body {
  overflow-x: hidden;
  min-height: 100vh;
  margin: 0;
  font-family: "Lato", "Arial", sans-serif;
  min-width: 320px;
  font-size: 16px;
  line-height: 1.2;
  font-weight: normal;
  color: #102746;
  background-color: #ffffff; }

main {
  min-height: calc(100vh - 386px); }

/*прижимает футер*/
.container {
  position: relative;
  max-width: 1060px;
  margin: 0 auto;
  padding: 0 100px;
  display: flex;
  flex-direction: column;
  background-color: transparent; }
  @media (max-width: 959px) {
    .container {
      padding-left: 40px;
      padding-right: 40px;
      margin: 0 auto; } }
  @media (max-width: 767px) and (min-width: 320px) {
    .container {
      padding-right: 20px;
      padding-left: 20px; } }

.container-1440 {
  max-width: 1440px;
  position: relative;
  margin: 0 auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="file"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

picture {
  display: block; }

.rub {
  font-family: "Lato", "Arial", sans-serif;
  font-size: inherit;
  font-weight: inherit; }

.btn {
  transition: box-shadow 0.2s linear;
  display: inline-block;
  vertical-align: top;
  padding: 10px 25px;
  color: #ffffff;
  text-align: center;
  box-shadow: 0 4px 10px rgba(98, 110, 122, 0.2);
  border: 0;
  border-radius: 50px;
  word-break: break-all;
  transition: all 0.3s ease;
  line-height: 21px;
  letter-spacing: 0;
  max-width: 300px;
  white-space: normal; }
  .btn:hover {
    transition: box-shadow 0.2s linear;
    box-shadow: 0 4px 20px rgba(98, 110, 122, 0.3); }
  .btn:active {
    box-shadow: none; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%; }

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  justify-content: space-between;
  padding-top: 56px; }

.header__list {
  display: flex;
  flex-wrap: wrap; }

.header__item {
  margin-right: 82px; }

.header__item:last-child {
  margin-right: 0; }

.header__link {
  font-size: 18px;
  line-height: 24px;
  color: white;
  text-decoration: none; }

.header__link:hover,
.header__link:focus,
.header__link:active {
  opacity: 0.75; }

/* Intro styles start */
.intro {
  height: 100vh;
  background: url(../img/intro-bg.jpg) no-repeat center;
  background-size: cover; }
